@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap);
.topbarContainer {
  height: 50px;
  width: 100%;
  background-color: #120a5c;
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.topbarLeft {
  flex: 3 1;
}

.logo {
  font-size: 24px;
  margin-left: 20px;
  font-weight: bold;
  color: white;
  cursor: pointer;
}

.topbarCenter {
  flex: 5 1;
  padding: 0 12px;
}

.searchbar {
  width: 100%;
  height: 30px;
  background-color: white;
  border-radius: 30px;
  display: flex;
  align-items: center;
}

.searchIcon {
  font-size: 20px !important;
  margin-left: 10px;
}

.searchInput {
  border: none;
  width: 70%;
}

.searchInput:focus {
  outline: none;
}

.topbarRight {
  flex: 4 1;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
}

.topbarLink {
  margin-right: 10px;
  font-size: 14px;
  cursor: pointer;
}

.topbarIcons {
  display: flex;
  margin-right: 12px;
  align-items: center;
}

.topbarIconItem {
  margin-right: 15px;
  cursor: pointer;
  position: relative;
}

.topbarIconBadge {
  width: 15px;
  height: 15px;
  background-color: red;
  border-radius: 50%;
  color: white;
  position: absolute;
  top: -5px;
  right: -5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.topbarImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

.searchbar .MuiAutocomplete-root {
  width: 100%;
  padding-left: 8px;
}

.searchbar .MuiAutocomplete-root .MuiFormControl-root label {
  font-size: 12px;
}

.searchbar .MuiAutocomplete-root .MuiInput-underline:before,
.searchbar .MuiAutocomplete-root .MuiInput-underline:after {
  border-bottom: none;
}

.searchbar
  .MuiAutocomplete-root
  .MuiInput-underline:hover:not(.Mui-disabled):before {
  border-bottom: none;
}

.searchbar .MuiAutocomplete-root .MuiInput-underline.Mui-focused:after {
  transform: none;
}

.searchbar .MuiAutocomplete-root label + .MuiInput-formControl {
  margin-top: 0;
}

.searchbar
  .MuiAutocomplete-root
  .MuiAutocomplete-inputRoot
  .MuiAutocomplete-input {
  font-size: 13px;
  padding-top: 0;
}

.searchbar .MuiAutocomplete-root .MuiInputLabel-formControl {
  top: -20px;
}

.searchbar .MuiInputLabel-shrink {
  top: -6px !important;
}

.searchSuggestions {
  display: flex;
  justify-content: baseline;
  align-items: center;
  border-bottom: 1px solid rgb(240, 240, 240);
  padding-bottom: 8px;
  width: 100%;
}

.searchSuggestions .profilePicture img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right: 16px;
  object-fit: cover;
}

.searchSuggestions .searchContent {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.searchSuggestions .email {
  font-size: 9px;
}

.searchSuggestions .username {
  font-size: 13px;
}

.helloUserText {
  font-weight: 500;
  padding-right: 8px;
}

.menuIcon {
  display: none;
  padding-left: 28px;
  padding-right: 8px;
}

@media only screen and (max-width: 782px) {
  .topbarRight {
    display: none;
  }
  .menuIcon {
    display: block;
  }
}

